.App {
  width: 95%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 auto;
}

.body {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-top: 1em;
}
