  .body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
  }
  .small-screen-spacer {
    display: none;
  }
}

.info-form {
  -ms-flex: 2 1;
      flex: 2 1;
  -ms-flex-item-align: stretch;
      align-self: stretch;
}

.side-info {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 2em;
}

.small-screen-spacer {
  margin: 2em 0;
  border-bottom: 1px solid grey;
}



